
class DropDownMenu {
    constructor() {
        this.$elRoot = null;
        this.$elDisplay = null;
        this.$elList = null;

        this.opened = false;

        this.prevIndex = 0;
    }

    init(rootQuery) {
        if(!window.$) {
            console.log("not define jquery");
            return;
        }
        this.initElement(rootQuery);
        this.bindEventHandler();
        this.$elList.hide();
    }

    initElement(rootQuery) {
        if(!$(rootQuery)) {
            console.log("not found element");
            return false;
        }
        this.$elRoot = $(rootQuery)

        if(!this.$elRoot.children('.display')) {
            console.log("not found child element");
            console.log("div.root");
            console.log("%c- div.display", 'font-weight: bold');
            console.log("- ul.list");
            return false;
        }
        this.$elDisplay = this.$elRoot.children('.display');

        if(!this.$elRoot.children('.list')) {
            console.log("not found child element");
            console.log("div.root");
            console.log("- div.display");
            console.log("%c- ul.list", 'font-weight: bold');
            return false;
        }
        this.$elList = this.$elRoot.children('.list');

        return true;
    }

    bindEventHandler() {
        this.$elDisplay.click( (e) => {
            if(!this.opened) {
                this.$elList.fadeToggle(200);
                this.opened = true;
            } else {
                this.$elList.fadeToggle(200);
                this.opened = false;
            }
        })

        this.$elList.find('li.drop-down-item').click( (e) => {
            this.$elDisplay.text($(e.target).text());
            this.$elList.hide();
        })
    }

    attachChangeHandler(changeEvent) {
        this.$elList.find('li.drop-down-item').click((e) => {
            let index = this.$elList.find('li.drop-down-item').index($(e.currentTarget));
            if(this.prevIndex === index) return;
            this.prevIndex = index;
            if(!changeEvent) return;
            changeEvent(index);
        })
    }
}

export default DropDownMenu;