import CardGenerator from '../../../common_ui/card_generator';

import LiveService from '../../../services/live.service';
import mainService from '../../../services/main.service';

const CONTENTS_PER_PAGE = 500;

export default (() => {
    let $video_list = null;
    let $total_count = null;
    let listData = [];
    let $load_contents_spinner = null;
    let _start = 0;
    let _contentsEnd = false;

    const init = () => {
        $video_list = $('.card-list');
        $total_count = $('#lbl-total .count');
        $load_contents_spinner = $('.spinner');
    }

    const getList = async (order, init) => {

        if (init) {
            $video_list.empty();
            listData = [];
            _contentsEnd = false;
            _start = 0;
        }

        if (_contentsEnd) return { state: false, status: "contents_end" };
        return new Promise(async (resolve, reject) => {
            $load_contents_spinner.show();

            const resGetChannelList = await mainService.getPopChannels(CONTENTS_PER_PAGE);


            const totalCount = resGetChannelList.data.channels.length;
            const list = resGetChannelList.data.channels;

            $total_count.text(totalCount);

            listData.push(...list);

            list.map((data) => {
                const { profile_img, nick_name, user_seq, subscription_cnt } = data;
                $video_list.append(
                    createCard(profile_img, nick_name, user_seq, subscription_cnt)
                )
            })

            bindEventListItems();

            if (list.length < CONTENTS_PER_PAGE) {
                _contentsEnd = true;
                $load_contents_spinner.hide();
                resolve({ state: false, status: "contents_end" });
            }

            _start = _start + CONTENTS_PER_PAGE;

            resolve({ state: true, status: "get_contents" });
        })
    }

    const createCard = (avatar, name, user_seq, subscription_cnt) => {
        return `
        <li class="channel-item" data-user-seq=${user_seq}>
            <div class="avatar-container">
                <img src="${avatar}">
            </div>
            <div class="name-container">
                <span class="bold">${name}</span>
            </div>
            <div class="subscribe-container">
                <span class="fs-xs fc-light-black">${i18next.t('subscriber')}&nbsp;<span class="fc-light-green">${subscription_cnt}</span></span>
            </div>

        </li>`
    }

    const bindEventListItems = () => {
        $('.channel-item').click((e) => {
            const userSeq = $(e.currentTarget).attr('data-user-seq');
            location.href = `/channel/${userSeq}`;
        })
    }

    return {
        init, getList
    }
})()


