import Fetch from '../utils/Fetch';
export default (() => {
    const getHotLives = () => {
        return Fetch({
            url: "/api/v1/live/popular?status=1003&length=4",
            type: 'GET',
            cache: false
        })
    }

    const getTopVideos = () => {
        return Fetch({
            url: "/api/v1/vod/popular?length=4",
            type: 'GET',
            cache: false
        })
    }

    const getPopChannels = (count) => {
        return Fetch({
            url: `/api/v1/channel/popular?length=${count}`,
            type: 'GET',
            cache: false
        })
    }

    return {
        getHotLives, getTopVideos, getPopChannels
    }
})()
